<template>
<div>
    <div class="columns">
        <div class="column is-12">
            <div class="box" style="display:inline-block; width:100%;">
                <div style="float:left"><p style="font-size:24px;">Account Ledger for Auto Journals</p></div>
                <div style="float:right">
                    <button class="button is-info" style="margin-right:5px;" @click.prevent="jeModal=true">Add JE</button>
                    <button class="button is-info" @click.prevent="mappingModal=true">Add Mapping</button>
                </div>
            </div>
        </div>
    </div>

    <div class="columns">
        <div class="column is-1"></div>
        <div class="column is-2">
            <div class="field is-horizontal">
                <div class="field-label is-normal">
                    <label class="label" style="font-size:18px; font-weight:normal; margin-left:10px;">Name:</label>
                </div>
                <div class="field-body">
                    <div class="field">
                        <p class="control" style="width:100%;">
                            <select v-model="idS" @change="SelectCOA()" style="height:40px">
                                <option value="" hidden>Trans. Proccess Id</option>
                                <option v-for="r in dataGL" :key="r.id">{{r.id}}</option>
                            </select>
                        </p>
                    </div>
                </div>
            </div>
        </div>

        <div class="column is-2">
            <div class="field is-horizontal">
                <div class="field-label is-normal">
                    <label class="label" style="font-size:18px; font-weight:normal; margin-left:10px;">Card:</label>
                </div>
                <div class="field-body">
                    <div class="field">
                        <p class="control" style="width:100%;">
                            <select v-model="cardS" style="height:40px">
                                <option value="" hidden>Card No</option>
                                <option v-for="r in dataC" :key="r.card_no">{{r.card_no}}</option>
                            </select>
                        </p>
                    </div>
                </div>
            </div>
        </div>
        
        <div class="column is-2">
            <div class="field is-horizontal">
                <div class="field-label is-normal">
                    <label class="label" style="font-size:18px; font-weight:normal; margin-left:10px;">Credit:</label>
                </div>
                <div class="field-body">
                    <div class="field">
                        <p class="control" style="width:100%;">
                            <input type="text" readOnly placeholder="credit" class="input" style="text-align:right;" v-model="creditS">
                        </p>
                    </div>
                </div>
            </div>
        </div>
        <div class="column is-2">
            <div class="field is-horizontal">
                <div class="field-label is-normal">
                    <label class="label" style="font-size:18px; font-weight:normal; margin-left:10px;">Debit:</label>
                </div>
                <div class="field-body">
                    <div class="field">
                        <p class="control" style="width:100%;">
                            <input type="text" readOnly placeholder="debit" class="input" style="text-align:right;" v-model="debitS">
                        </p>
                    </div>
                </div>
            </div>
        </div>
         <div class="column is-1">
             <button class="button is-info">Submit</button>
         </div>
    </div>

    <!-- Modal: Mapping add -->
    <div v-if="mappingModal == true">
        <div class="modal is-active" style="align-items: start; flex-direction: row; justify-content: end;">
            <div class="modal-background" @click.prevent="mappingModal = false"></div>
                <div class="modal-content" style="background:white; max-height: 100%;min-height: 100%; width:800px; margin-right:0;">

                    <div class="box" style="height:100vh;">
                        <div class="columns" style="height:10vh;">
                            <div class="column is-12">
                                <div class="box" style="height:7vh;">
                                    <p style="font-size:20px;">Create General Ledger Mapping</p>
                                </div>
                            </div>
                        </div>

                        <div class="columns" style="height:70vh; margin-top:15px;">
                            <div class="column is-12">
                                <div class="box" style="height:70vh;">

                                    <div class="field is-horizontal">
                                        <div class="field-label is-normal">
                                            <label class="label" style="font-size:20px; width:150px;">ID:</label>
                                        </div>
                                        <div class="field-body">
                                            <div class="field">
                                                <p class="control" style="width:300px;">
                                                    <input class="input" type="text" placeholder="id" v-model="id">
                                                </p>
                                            </div>
                                        </div>
                                    </div>

                                    <div class="field is-horizontal">
                                        <div class="field-label is-normal">
                                            <label class="label" style="font-size:20px; width:150px;">Name:</label>
                                        </div>
                                        <div class="field-body">
                                            <div class="field">
                                                <p class="control" style="width:300px;">
                                                    <input class="input" type="text" placeholder="name" v-model="name">
                                                </p>
                                            </div>
                                        </div>
                                    </div>

                                    <div class="field is-horizontal">
                                        <div class="field-label is-normal">
                                            <label class="label" style="font-size:20px; width:150px;">Credit:</label>
                                        </div>
                                        <div class="field-body">
                                            <div class="field">
                                                <p class="control" style="width:300px;">
                                                    <select v-model="credit" style="width:300px; height:40px">
                                                        <option value="" hidden>Credit</option>
                                                        <option v-for="r in dataCOA" :key="r.id">{{r.id}}</option>
                                                    </select>
                                                </p>
                                            </div>
                                        </div>
                                    </div>

                                    <div class="field is-horizontal">
                                        <div class="field-label is-normal">
                                            <label class="label" style="font-size:20px; width:150px;">Debit:</label>
                                        </div>
                                        <div class="field-body">
                                            <div class="field">
                                                <p class="control" style="width:300px;">
                                                    <select v-model="debit" style="width:300px; height:40px">
                                                        <option value="" hidden>Debit</option>
                                                        <option v-for="r in dataCOA" :key="r.id">{{r.id}}</option>
                                                    </select>
                                                </p>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>

                        <!-- Buttons -->
                        <div style="margin-top:25px;">
                            <button class="button is-danger is-light" @click.prevent="mappingModal = false" style="margin-right:5px;">Cancel</button>
                            <button class="button is-success is-light" @click.prevent="mappingAdd()" >Submit</button> 
                        </div>
                    </div>
                </div>
            </div>
    </div>
</div>
</template>

<script>
    import api from '../boot/axios'
    import { toast } from 'bulma-toast'

    export default {
        data() {
            return {
                dataCOA:[],
                dataTT:[],
                dataGL:[],
                dataC:[],
                mappingModal: false,
                jeModal: false,
                id:'',
                name:'',
                credit:'',
                debit:'',
                idS:'',
                cardS:'',
                creditS:'',
                debitS:'',
            }
        },
        created() {
            this.getData();
        },
        methods:{

            async getData(){
                await api
                    .get('api/Transaction_table/')
                    .then(response => {
                        this.dataTTA = response.data
                        console.table(this.dataTTA);
                    })
                    .catch(error => {
                        console.log(error)
                    })
                await api
                    .get('api/coa/')
                    .then(response => {
                        this.dataCOA = response.data
                        console.table(this.dataCOA);
                    })
                    .catch(error => {
                        console.log(error)
                    });
                await api
                    .get('api/gl_mapping/')
                    .then(response => {
                        this.dataGL = response.data
                        console.table(this.dataGL);
                    })
                    .catch(error => {
                        console.log(error)
                    });
                await api
                    .get('api/card/')
                    .then(response => {
                        this.dataC = response.data
                        console.table(this.dataC);
                    })
                    .catch(error => {
                        console.log(error)
                    });
            },

            async mappingAdd(){
                const a = {
                    id: this.id,
                    name: this.name,
                    credit: this.credit,
                    debit:this.debit
                }

                await api
                    .post('api/gl_mapping/',a)
                    .then(()=>{
                        toast({
                            message: 'Mapping has added',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 1000,
                            position: 'bottom-right',
                        })
                        this.id='';
                        this.name='';
                        this.credit='';
                        this.debit='';
                        this.getData();
                        this.mappingModal=false;
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },

            SelectCOA(){
                console.table(this.dataGL);
                for (let i = 0; i < this.dataGL.length; i++) {
                    if (this.dataGL[i].id==this.idS) {
                        this.creditS=this.dataGL[i].credit;
                        this.debitS=this.dataGL[i].debit;
                        break;
                    }
                }

            }

        

        }
    
}
</script>