                            </div>

                            <div class="columns" style="height:70vh; margin-top:15px;">
                                <div class="column is-12">
                                    <div class="box" style="height:70vh;">

                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Color:</label>
                                            </div>
                                            <div class="field-body">
                                            <div class="control">
                                                <!-- <select v-model="dataRTE.color" style="width:300px; height:40px">
                                                    <option value="" hidden>Select Color</option>
                                                    <option :style="{'background-color': `${r}`}" v-for="r in color" :key="r">{{r}}</option>
                                                </select> -->
                                                <input class="input" type="color" placeholder="type name" v-model="dataRTE.color" style="width:100px;">
                                            </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Cost:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="field">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="text" placeholder="cost" v-model="dataRTE.cost">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Discount:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="field">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="text" v-model="dataRTE.discount">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Bed:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="field">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="number" min="0" v-model="dataRTE.bed">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Short Form:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="field">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="text" placeholder="short" v-model="dataRTE.short">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>

                            <!-- Buttons -->
                            <div style="margin-top:25px;">
                                <button class="button is-danger is-light" @click.prevent="DeleteRoomType()" style="margin-right:5px;">Delete</button>
                                <button class="button is-warning is-light" @click.prevent="roomTME = false" style="margin-right:5px;">Cancel</button>
                                <button class="button is-success is-light" @click.prevent="EditRoomType()">Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
        </div>

        <div v-if="rejectModal == true">
            <div class="modal is-active" >
                <div class="modal-background" @click.prevent="rejectModal = false" ></div>
                <div class="modal-content" style="background:white; height: 80vh; width: 40vw; overflow-x: hidden;">

                    <div style="margin-left:70px; margin-top:5vh;">
                        <div class="field is-horizontal">
                            <div class="field-label is-normal">
                                <label class="label" style="font-size:20px;">Template Chosen</label>
                            </div>
                            <div class="field-body">
                            <div class="control">
                                <select v-model="templateValue" style="width:400px; height:40px" disabled>
                                    <option value="" hidden>Select Room</option>
                                    <option v-for="r in templateData" :key="r.name" :value="r.name">{{r.name}} || {{r.role_concated}}</option>
                                </select>
                            </div>
                            </div>
                        </div>
                        <div class="field is-horizontal" style="">
                        <div class="field-label is-normal">
                            <label class="label" style="font-size:20px;">Room Type:</label>
                        </div>
                        <div class="field-body">
                        <div class="control">
                            <select v-model="room_type" style="width:300px; height:40px" disabled>
                                <option value="" hidden>Select Room</option>
                                <option v-for="r in dataRT" :key="r.type_name">{{r.type_name}}</option>
                            </select>
                        </div>
                        </div>
                        </div>
                        <div class="field is-horizontal" >
                            <div class="field-label is-normal" style="">
                            <label class="label" style="font-size:20px; ">Reject Reason:</label>
                            </div>
                            <div class="field-body">
                                <div class="field">
                                    <p class="control" style="width:300px;">
                                    <textarea class="textarea" placeholder="Reason" v-model="rejectReason" style=""></textarea>
                                    </p>
                                </div>
                                </div>
                        </div>
                        

                        
                        <div class="field is-horizontal">
                            <div class="field-label is-normal">
                                <label class="label" style="font-size:20px;">Price</label>
                            </div>
                            <div class="field-body">
                                <div class="field">
                                    <p class="control" style="width:300px;">
                                        <input class="input" type="text" placeholder="cost" v-model="cost">
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div class="field is-horizontal">
                            <div class="field-label is-normal">
                                <label class="label" style="font-size:20px;">Discount</label>
                            </div>
                            <div class="field-body">
                                <div class="field">
                                    <p class="control" style="width:300px;">
                                        <input class="input" type="text" placeholder="discount" v-model="discount">
                                    </p>
                                </div>
                            </div>
                        </div>
                        
                        <div class="field is-horizontal">
                            <div class="field-label is-normal">
                                <label class="label" style="font-size:20px;">Effective Date</label>
                            </div>
                            <div class="field-body">
                                <div class="field">
                                    <p class="control" style="width:300px;">
                                        <input type="date" class="input" v-model="date">
                                    </p>
                                </div>
                            </div>
                        </div>
                        
                        
                    </div>
                                        
                                  
                    <button class="button" style="margin-top:20px;" @click.prevent ="rejectSubmit()">Submit</button>
                </div>
            </div>
        </div>

        <!-- ModaL: Approval -->
        <div v-if="statusB == true">
            <div class="modal is-active">
                <div class="modal-background" @click.prevent="statusB = false">
                    <div class="modal-content" style="height:30vh; background:white; margin-top:25vh;">
                        <div style="margin-top:15%;">
                            <p class="subtitle is-4">Pricing has approved?</p>
                            <div style="margin-top:25px">
                                <button class="button is-info is-black" @click.prevent="ChangeStatus()">Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</div>
</template>

<script>
    import api from '../boot/axios'
    import { toast } from 'bulma-toast'

    export default {
        data() {
            return {
                isActive: 'type',
                id:'',
                idC:'',
                idD:'',
                idRoomType:'',
                dataFlow: [],
                dataColor:[],
                dataR: [],
                dataRE: [],
                dataRT: [],
                dataRTE: [],
                roomTypeArr:[],
                roomTM: false,
                roomTME: false,
                roomAdd: false,
                roomAddE: false,
                flowAdd: false,
                statusB: false,
                type_name:'',
                cost:'',
                discount:0,
                short:'',
                bed:0,
                roomTypeNameE:'',
                roomE:'',
                room_no:'',
                room_color:'',
                room_type:'',
                extra_bed:false,
                adults:0,
                child:0,
                floor:'',
                view:'',
                features:'',
                date:'',
                templateData: [],
                templateValue: '',
                templatelevelData: '',
                rejectModal: false,
                rejectReason: '',
                rejectTransferId: '',

            }
        },
        created() {
           this.getData();
           this.getDataR();
           this.getDataFlow();
           this.getTemplateData();
        },
        methods:{
            async showRejectReason(id){
                this.rejectTransferId = id
                this.rejectModal = true
                this.templateValue = id.template
                this.rejectReason = id.reject_reason
                this.room_type = id.room_type
                this.cost = id.proposedCost
                this.discount = id.proposedDiscount
                this.date = id.activataionDate



            },
            async rejectSubmit(){
                console.log(this.rejectTransferId)
                await api.put(`api/room_type_flow/${this.rejectTransferId.id}/`,{
                    room_type:this.rejectTransferId.room_type,
                    proposedCost:this.cost,
                    proposedDiscount:this.discount,
                    activataionDate:this.date,
                    status:'pending',
                    template: this.rejectTransferId.workflow_name,
                    status_role: 'ongoing'
                }).catch(err => console.log(err))

                

                await this.getDataFlow()
                this.date = ''
                this.discount = ''
                this.cost = ''
                this.room_type = ''
                this.rejectModal = false
            },
            async getTemplateData() {
                await api.get('api/workflow_template/'
                    ).then(res => {
                        this.templateData= res.data
                    }).catch(err => console.log(err))
                    console.log('template data', this.templateData)

                    
            },
            async getData() {
                await api
                    .get('api/room_type/')
                    .then(response => {
                        this.dataRT = response.data
                        console.table(this.dataRT);
                    })
                    .catch(error => {
                        console.log(error)
                    })
                    for (let i = 0; i < this.dataRT.length; i++) {
                        this.roomTypeArr.push(this.dataRT[i].type_name) 
                        this.dataColor.push(this.dataRT[i].color)
                    }
            },

            async getDataFlow(){
                await api
                    .get('api/room_type_flow/')
                    .then(response => {
                        this.dataFlow = response.data
                        console.table(this.dataFlow);
                    })
                    .catch(error => {
                        console.log(error)
                    })
                    
            },

            async getDataR() {
                await api
                    .get('api/room/')
                    .then(response => {
                        this.dataR = response.data
                        console.table(this.dataR);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },

            async getDataE() {
                const q = this.roomTypeNameE;
                await api
                    .get(`api/room_type/${q}/`)
                    .then(response => {
                        this.dataRTE = response.data
                        console.log(this.dataRTE);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },

            async getDataEE() {
                const q = this.roomE;
                await api
                    .get(`api/room/${q}/`)
                    .then(response => {
                        this.dataRE = response.data
                        console.log(this.dataRE);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },

            async AddRoom(){
                const a = {
                    room_no:this.room_no,
                    room_type:this.room_type,
                    extra_bed:this.extra_bed,
                    adults:this.adults,
                    child:this.child,
                    floor:this.floor,
                    view:this.view,
                    status_fd:'vacant',
                    status_hk:'clean',
                    features:this.features,
                }
                await api
                    .post('/api/room/', a)
                    .then(() => {
                        toast({
                            message: 'Room has added',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        this.room_no='';
                        this.room_type='';
                        this.extra_bed=false;
                        this.adults= 0;
                        this.child=0;
                        this.floor='';
                        this.view='';
                        this.features='',

                        this.getDataR();
                        
                        this.roomAdd=false;

                    })
                    .catch(err => {
                        console.log(err)
                    })

            },

            async AddRoomType(){
                const a = {
                    type_name:this.type_name,
                    cost:this.cost,
                    discount:this.discount,
                    services: '',
                    bed:this.bed,
                    color:this.room_color,
                    short: this.short
                }
                await api
                    .post('/api/room_type/', a)
                    .then(() => {
                        toast({
                            message: 'Room type has added',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        this.type_name='';
                        this.cost='';
                        this.discount=0;
                        this.bed=0;
                        this.short='';
                        this.dataRT=[];
                        this.dataColor=[];
                        this.getData();
                        this.getDataR();
                        
                        this.roomTM=false;
                        // this.$router.push('/settings_room');

                    })
                    .catch(err => {
                        console.log(err)
                    })

            },

            roomModal(t){
                this.roomTypeNameE=t;
                this.getDataE();
                this.roomTME=true;
            },

            roomEditModal(t){
                this.roomE=t;
                this.getDataEE();
                this.roomAddE=true;
            },

            async EditRoomType(){
                const t = this.roomTypeNameE
                await api
                    .patch(`/api/room_type/${t}/`, this.dataRTE)
                    .then(response => {
                        toast({
                            message: 'Room type edited',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                        this.dataColor=[];
                        this.dataRT=[];
                        this.getData();
                        this.getDataR();
                        this.roomTME = false;
                        this.$router.push('/settings_room');
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },

            async DeleteRoomType(){
                const t = this.roomTypeNameE
                await api
                    .delete(`/api/room_type/${t}/`)
                    .then(response => {
                        toast({
                            message: 'Room type deleted',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                        this.dataRTE;
                        this.dataColor=[];
                        this.dataRT=[];
                        this.getData();
                        this.roomTME = false;
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },

            async EditRoom(){
                const t = this.roomE
                console.log("lol",this.dataRE);
                await api
                    .patch(`/api/room/${t}/`, this.dataRE)
                    .then(response => {
                        toast({
                            message: 'Room edited',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                        this.getDataR();
                        this.roomAddE = false;
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },

            async DeleteRoom(){
                const t = this.roomE
                await api
                    .delete(`/api/room/${t}/`)
                    .then(response => {
                        toast({
                            message: 'Room deleted',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                        this.getDataR();
                        this.roomAddE = false;
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },

            flowEditModal(t){
                console.log(t);
            },

            async AddFlow(){

                await api.get(`api/workflow_template_levels/?workflow_name=${this.templateValue}`
                    ).then(res => {
                        this.templatelevelData = res.data[0]
                    }).catch(err => console.log(err))
                    console.log(this.templatelevelData)
                const a ={
                    room_type:this.room_type,
                    proposedCost:this.cost,
                    proposedDiscount:this.discount,
                    activataionDate:this.date,
                    status:'pending',
                    template: this.templatelevelData.workflow_name,
                    depends_id: this.templatelevelData.depend_id,
                    depends_on: this.templatelevelData.depend_on,
                    role_user_id: this.templatelevelData.depend_role,
                    status_role: 'ongoing'

                }
                console.log(a)
                await api
                    .post('/api/room_type_flow/', a)
                    .then(() => {
                        toast({
                            message: 'Flow has added',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        this.type_name='';
                        this.cost='';
                        this.discount=0;
                        this.date='';
                        this.getDataFlow();
                        
                        this.flowAdd=false;

                    })
                    .catch(err => {
                        console.log(err)
                    })

            },

            async ChangeStatus(){
                await api
                .put(`api/room_type_flow/${this.id}/`,{status: 'approved'})
                .catch(error => {
                        console.log(error)
                    })
                 this.getDataFlow();

                 await api
                .put(`api/room_type/${this.idRoomType}/`,{type_name: this.idRoomType, cost: this.idC, discount: this.idD})
                .catch(error => {
                        console.log(error)
                    })
                 this.getData();
                 
                this.statusB=false;
            },

        }
    }
</script>

<style>
.tab-a .a {
  display: none;
}
.tab-a .a.is-active {
  display: block;
}
ul li{
    float: left
}
</style>