<template>
<div style="height:100vh;">
    <div class="columns">
        <div class="column is-12">
            <div class="box">
                <p style="font-size:22px; text-align:left;">Stock</p>
            </div>
        </div>
    </div>
    <div class="columns" style="margin-left:20px;">
        <div class="column is-2"></div>
        <div class="column is-2">
            <div class="select is-fullwidth"><select v-model="filterSelection">
                <option value="" hidden><b>Filter Selection:</b></option><option v-for="filter in filterTypes" :key="filter.id">
                    {{filter}}
                </option></select>
                </div>
        </div>
        <div class="column is-2">
            <div class="field">
                <div class="control">
                    <input type="date" class="input" v-model="date">
                </div>
            </div>
        </div>
    </div>
    <div class="columns" >
        <div class="column is-2"></div>
        <div class="column is-8">
            <div class="box" style="height:60vh;">
                 <table class="table is-fullwidth">
                    <thead>
                        <tr>
                            <!-- <th>Room</th> -->
                            <!-- <th>Date</th> -->
                            <th>Item Name</th>
                            <th>D. Quantity</th>
                            <th>U. Quantity</th>
                            <th>Cost</th>
                        </tr>
                    </thead>

                    <tbody>
                        <tr v-for="d in data" v-bind:key="d.id">
                            <template v-if="d.room_no == filterSelection">
                                <template v-if="d.date==date">
                                    <!-- <td>{{ d.room_no }}</td> -->
                                    <!-- <td>{{ d.date }}</td> -->
                                    <td>{{ d.item_name}}</td>
                                    <td>{{ d.defaultQuantity }}</td>
                                    <td>{{ d.item_used }}</td>
                                    <td>{{ d.item_bill }}</td>
                                </template>
                            </template>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="column is-2"></div>
    </div>
</div>
</template>

<script>
    import api from '../boot/axios'
    // import { toast } from 'bulma-toast'

    export default {
        data() {
            return {
                data: [],
                dataR: [],
                dataS: [],
                filterSelection: '',
                filterTypes: [],
                dftQ: '',
                dftN: '',
                date:new Date().toISOString().substr(0, 10),
            }
        },
        created() {
            this.getDataS();
            this.getDataR();
            this.getDataST();
        },
        methods:{

            async getDataS() {
                await api
                    .get('api/stockjoint/')
                    .then(response => {
                        this.data = response.data
                        console.log(this.data);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },

            async getDataST() {
                await api
                    .get('api/item/')
                    .then(response => {
                        this.dataS = response.data
                        console.log(this.dataS);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },

            async getDataR() {
                await api
                    .get('api/room/')
                    .then(response => {
                        this.dataR = response.data
                        console.log(this.dataR);
                    })
                    .catch(error => {
                        console.log(error)
                    })
                    for (let i = 0; i < this.dataR.length; i++) {
                        this.filterTypes.push(this.dataR[i].room_no)
                    }
                    console.log(this.filterTypes);
            },
        }
    }
</script>