                            <div class="field">
                                <p class="control" style="width:300px;">
                                    <input
                                        type="file"
                                        style="min-width: 70%"
                                        label="User Image"
                                        autogrow
                                        @change="OnFileUpload"
                                    />
                                </p>
                            </div>
                        </div>
                    </div>                  
                    <button class="button" style="margin-top:5px; background-color:#26ADE4; color:white" @click.prevent="Submit()">Save</button>

                </div>

            </div>
        </div>
