    </div>
</div>
</template>

<script>
    import api from '../boot/axios'
    
    import { toast } from 'bulma-toast'

    export default {
        data() {
            return {
                data:[],
                logo_array:[],
            }
        },
        created() {
            this.getData();
            this.getLogo();
        },
        methods:{

            async getData() {
                await api
                    .get('auth/company/1/')
                    .then(response => {
                        this.data = response.data
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },
            async getLogo(){
                await api.get('/auth/hotel_logo/1/'
                ).then(res =>{

                            this.logo_array = res.data;
                            
                        }).catch(err => console.log(err))
            },
            async Submit(){
                await api
                    .patch('/auth/company/1/', this.data)
                    .then(response => {
                        toast({
                            message: 'Saved',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                    })
                    .catch(err => {
                        console.log(err)
                    })
                    window.location.reload();

                    await this.uploadImageHandler()
            },
            OnFileUpload(event){
                this.pot = event.target.files[0]
                // console.log(event)
            },
            async uploadImageHandler(){
                const fd = new FormData();

                    fd.append("pic", this.pot);
                    fd.append("hotel_id", this.data.id)
              

                const config = {
                    headers: {
                    "Content-Type": "multipart/form-data"
                    }
                }
            
                await api.put(`/auth/hotel_logo/1/`, fd, config)
                    .then(res => {
                    console.log('pic upload: ',res.data)
                    })
            }
            
        },
    }
    
</script>