                                                <td>
                                                    <!-- <button class="button is-info is-small" @click="ItemEdit(d.name)"><i class="fa-solid fa-pen" style="font-size:10px;"></i></button> -->
                                                    <button class="button is-danger is-small" style="margin-left:5px;" @click="DeleteItem(d.name)"><i class="fa-solid fa-xmark" style="font-size:15px;"></i></button>
                                                </td>
