                                        </tr>
                                    </thead>

                                    <tbody>
                                        <tr v-for="d in dataItem" v-bind:key="d.name">                                         
                                            <template v-if="d.item_type=='miniBar'">
                                                <td>{{ d.name }}</td>
                                                <td>{{ d.defaultQuantity }}</td>
                                                <td>{{ d.price }}</td>
