</template>


<script>
    import api from '../boot/axios'
    import { toast } from 'bulma-toast'

    export default {
        data() {
            return {
                isActive: 'bedroom',
                dataI:[],
                dataItem:[],
                dataItemSeleted:[],
                itemEditM:false,
                itemV:'',
                name:'',
                item_type:'',
                defaultQuantity:'',
                price:'',
                is_vat:false,
                vat:'',
            }
        },
        created() {
            this.getDataI();
            this.getDataItem();
        },
        methods:{

            async getDataI() {
                await api
                    .get('api/inventory_view/')
                    .then(response => {
                        this.dataI = response.data
                        console.log(this.dataI);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },
            
            async getDataItem() {
                await api
                    .get('api/item/')
                    .then(response => {
                        this.dataItem = response.data
                        console.log(this.dataItem);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },

            async getDataItemSeleted(t) {
                await api
                    .get(`/api/item/${t}/`)
                    .then(response => {
                        this.dataItemSeleted = response.data
                        console.log(this.dataItemSeleted);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },

            async AddData(t){

                if (t=='washroom'||t=='bedroom') {
                    this.price=0;
                    this.vat=0;
                }

                if (this.name=='' || this.defaultQuantity=='') {
                    toast({
                            message: 'Feilds can not be empty',
                            type: 'is-warning',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                    return;
                }
                const a ={
                    name:this.name,
                    item_type:t,
                    defaultQuantity:this.defaultQuantity,
                    price:this.price,
                    is_vat:this.is_vat,
                    vat:this.vat,
                }
                await api
                    .post('/api/item/', a)
                    .then(() => {
                        toast({
                            message: 'Item added',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })

                        this.name='',
                        this.item_type='',
                        this.defaultQuantity='',
                        this.price='',
                        this.is_vat=false,
                        this.vat='',

                        this.getDataItem();
                        
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },

            async DeleteItem(t){
                await api
                    .delete(`/api/item/${t}/`)
                    .then(response => {
                        toast({
                            message: 'Item deleted',
                            type: 'is-warning',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 1000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                        this.getDataItem();
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },

            ItemEdit(t){
                this.getDataItemSeleted(t);
                this.itemV=t;
                this.itemEditM=true;
            },

            async SubmitEdit(){
                const t = this.itemV
                await api
                    .patch(`/api/item/${t}/`, this.dataItemSeleted)
                    .then(response => {
                        toast({
                            message: 'Item edited',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                        this.getDataItem();
                        this.dataItemSeleted=[];
                        this.itemEditM = false;
                    })
                    .catch(err => {
                        console.log(err)
                    })
            }
        }
            
    }
</script>

<style>
.tab-a .a {
  display: none;
}
.tab-a .a.is-active {
  display: block;
}
ul li{
    float: left
}
</style>