    async searchDate(){
      this.roomVacantData = []
      this.roomVacantDataTemp =[]
      this.roomBooked = []
      this.roomData = []
      let bookedBol = false;
      this.resData = []

      await getAPI.get('rmrt/'
      ).then(res =>{
        this.roomData = res.data
        //console.log("room",this.roomData)
      }).catch(err => console.log(err))

      await getAPI.get('reservation/'
      ).then(res =>{
        this.dataR = res.data
        //console.log("res",this.resData)
      }).catch(err => console.log(err))

      

       let varr = [];
        for (let i = 0; i < this.dataR.length; i++) {
                const a = {
                  room:parseInt(this.dataR[i].room),
                  check_in_date:this.dataR[i].check_in_date,
                  check_out_date:this.dataR[i].check_out_date,
                  status:this.dataR[i].status,
                }
                varr.push(a);
                if (this.dataR[i].secondary_room!="" && this.dataR[i].secondary_room != null) {
                  
                  let b =this. dataR[i].secondary_room.split(",");
                  for (let j = 0; j < b.length; j++) {
                    const c = {
                      room:parseInt(b[j]),
                      check_in_date:this.dataR[i].check_in_date,
                      check_out_date:this.dataR[i].check_out_date,
                      status:this.dataR[i].status,
                    }
                    varr.push(c);
                  }
            }
        }

        this.dataR=varr;
        console.table(this.dataR);
      
      
      if(this.cd != '' && this.co != ''){
        for(let i=0; i< this.roomData.length; i++){
            this.b1=false;
            this.b2=false;
            this.b3=false;
            this.b4=false;
            this.b5=false;

          for(let a=0; a< this.dataR.length; a++){
            if(this.dataR[a].status == 'booked' && bookedBol == false && this.dataR[a].status != "OnHold"){

              this.roomBooked.push(this.dataR[a])
              bookedBol = true
            }
            
            

            if(this.roomData[i].room_no == this.dataR[a].room && this.dataR[a].status != 'booked'){
              if(this.dataR[a].status == 'OnHold'){
                this.b5 = true
                break
              }
              if(this.cd > this.dataR[a].check_in_date && this.cd < this.dataR[a].check_out_date){
                console.log(true)
                this.b1 = true
                break;
              }
              if(this.co > this.dataR[a].check_in_date && this.co < this.dataR[a].check_out_date){
                console.log(true)
                this.b2 = true
                break;
              }
              if(this.dataR[a].check_in_date> this.co && this.dataR[a].check_in_date < this.cd){
                console.log(true)
                this.b3 = true
                break;
              }
              if(this.dataR[a].check_out_date> this.co && this.dataR[a].check_out_date < this.cd){
                console.log(true)
                this.b4 = true
                break;
              }
              if (this.dataR[a].check_in_date==this.co && this.dataR[a].check_out_date == this.cd) {
                this.b1=true
                break
              }
            }
          }
          if(this.b1 == false && this.b2 == false && this.b3 == false && this.b4 == false && this.b5 == false){
            this.roomVacantData.push(this.roomData[i])
          }
        }
        this.roomVacantDataTemp = this.roomVacantData
        console.log("temp",this.roomVacantDataTemp)
      }
       
      // console.log("presort",this.roomVacantData)
      // this.roomVacantData.sort()
      // console.log("postsort",this.roomVacantData)
      this.conDateS = new Date(this.co)
      this.conDateE = new Date(this.cd)
      this.dayCount = (this.conDateE- this.conDateS) / (1000*3600*24)
      console.log("yolo",this.roomBooked)
    
    },
