                                            <div class="box" style="height:7vh;">
                                                <p style="font-size:20px; float:left;">Order No. {{orderCMOdal}}</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="columns" style="height:8vh;">
                                        <div class="column is-3" style="font-size:18px;">
                                            Name: {{nameM}}
                                        </div>
                                        <div class="column is-3" style="font-size:18px;">
                                            Phone no. {{phoneM}}
                                        </div>
                                        <div class="column is-3" style="font-size:18px;">
                                            Total Item(s): {{itemTM}}
                                        </div>
                                        <div class="column is-3" style="font-size:18px;">
                                            Total Cost: {{costM}}
                                        </div>
                                    </div>

                                    <div class="columns" style="height:70vh; width:100%;">
                                        <div class="column is-12">
                                            <table class="table is-fullwidth">
                                                <thead>
                                                    <tr>
                                                        <th>Gender</th>
                                                        <th>Item</th>
                                                        <th>Type</th>
                                                        <th>Quantity</th>
                                                        <th>Cost</th>
                                                    </tr>
                                                </thead>

                                                <tbody>
                                                    <tr v-for="d in orderD" v-bind:key="d.n">       
                                                        <td>{{ d.q }}</td>
                                                        <td>{{ d.w }}</td>
                                                        <td>{{ d.e }}</td>
                                                        <td>{{ d.r }}</td>
                                                        <td>{{ d.t }}</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>

                                    <!-- Buttons -->
                                    <div>
                                        <button class="button is-danger is-light" @click.prevent="detailsModalCustomer2 = false,orderD=[]" style="margin-right:5px;">Cancel</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                </div>

            </div>
        </div>
    </div>
</div>
</template>

<script>
import api from '../boot/axios'
import { toast } from 'bulma-toast'

    export default {
        data() {
            return {
                hotel:[],
                dataA: [],
                dataCost:[],
                dataR: [],
                itemM:[],
                itemF:[],
                varr:[],
                orderD:[],
                statusB:false,
                roomE: false,
                detailsModalCustomer:false,
                detailsModalCustomer2:false,
                id:'',
                isD: false,
                isActive: 'guest',
                addGuest: false,
                addCustomer: false,
                room_no:'',
                res_id:'',
                res_var:'',
                date_received: new Date().toISOString().substr(0, 10),
                ex_date_delivery: '',
                total_item:0,
                total_cost:0,
                customer_name:'',
                customer_phone:'',
                gender:'',
                item:'',
                lType:'',
                quantity:0,
                cardM:'',
                nameM:'',
                phoneM:'',
                roomM:'',
                costM:0,
                itemTM:0,
                orderCMOdal:''
               }
        },
        mounted() {
            this.getData();
            this.getDataF();
            this.getHotel();
        },
        methods: {
            async getHotel(){
            await api.get('/auth/company/1/'
                ).then(res =>{
                    this.hotel = res.data
                    console.log(this.hotel);
                }).catch(err => console.log(err))
            },

            async getData() {
                await api
                    .get('api/laundry/')
                    .then(response => {
                        this.dataA = response.data
                    })
                    .catch(error => {
                        console.log(error)
                    })
                
                await api
                    .get('api/laundry_cost/')
                    .then(response => {
                        this.dataCost = response.data
                    })
                    .catch(error => {
                        console.log(error)
                    })
                    this.VarMethod();

            },

            VarMethod(){
                this.itemM=[];
                this.itemF=[];
                this.dataCost.forEach(r => {
                        if (r.gender_type=='Male') {
                            this.itemM.push(r)
                        }
                        if (r.gender_type=='Female') {
                            this.itemF.push(r)
                        }
                    });
            },

            async getDataF() {
                await api
                    .get('api/card/')
                    .then(response => {
                        this.dataR = response.data
                    })
                    .catch(error => {
                        console.log(error)
                    })
                    // let varr = [];
                    // for (let i = 0; i < this.dataR.length; i++) {
                    //     if (this.dataR[i].checked_in==true) {
                    //         varr.push(this.dataR[i])
                    //     }
                    // }
                    // this.dataR=varr;

                    let varr = [];
                    console.table(this.dataR);
                    for (let i = 0; i < this.dataR.length; i++) {
                        if (this.dataR[i].checked_in==true) {
                            const a = {
                              card_no:this.dataR[i].card_no,
                              room_no:parseInt(this.dataR[i].room_no),
                              rev_id:this.dataR[i].rev_id
                            }
                            varr.push(a);
                            if (this.dataR[i].secondary_room!="''") {
                              let b =this. dataR[i].secondary_room.split(",");
                              for (let j = 0; j < b.length; j++) {
                                const c = {
                                  card_no:this.dataR[i].card_no,
                                  room_no:parseInt(b[j]),
                                  rev_id:this.dataR[i].rev_id
                                }
                                varr.push(c);
                              }
                            }
                        }
                    }
                    this.dataR=varr;
                    console.table(this.dataR);
            },

            async changeStatus(){
                await api
                .put(`api/laundry/${this.id}/`,{status: true,claimed_date:new Date().toISOString().substr(0, 10),claimed_time:new Date().toISOString().substr(11, 5)})
                .catch(error => {
                        console.log(error)
                    })
                 this.getData();
                 
                this.statusB=false;
            },

            async submitGuestInfo(){
                let b = '';
                for (let i = 0; i < this.varr.length; i++) {
                    b+=this.varr[i].gender+','
                    b+=this.varr[i].item+','
                    b+=this.varr[i].wash+','
                    b+=this.varr[i].quantity+','
                    b+=this.varr[i].cost+'|'
                }
                b = b.slice(0, -1);

                if (this.room_no=='' || this.res_id=='') {
                    toast({
                            message: 'Room/Card no is blank',
                            type: 'is-warning',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                    })
                    return;
                }
                if (this.date_received=='' || this.ex_date_delivery=='') {
                    toast({
                            message: 'Date blank',
                            type: 'is-warning',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                    })
                    return;
                }
                if (b=='') {
                    toast({
                            message: 'Order is Blank',
                            type: 'is-warning',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                    })
                    return;
                }
                this.total_cost = parseInt(this.total_cost+(this.total_cost*(this.hotel.laundry_tax/100))+(this.total_cost*(this.hotel.laundry_sc/100)))
                let customer=[];
                await api
                    .get(`api/reservation/${this.res_var}/`)
                    .then(response => {
                        customer = response.data
                        console.log(customer);
                    })
                    .catch(error => {
                        console.log(error)
                    })
                customer.total_due=parseInt(this.total_cost)+parseInt(customer.total_due);
                customer.total_amount=parseInt(this.total_cost)+parseInt(customer.total_amount);
                await api
                    .put(`api/reservation/${this.res_var}/`,customer)
                    .catch(error => {
                        console.log(error)
                    })
                const a = {
                    room_no:this.room_no,
                    card_no:this.res_id,
                    date_received: this.date_received,
                    ex_date_delivery: this.ex_date_delivery,
                    total_item:this.total_item,
                    total_cost:this.total_cost,
                    status:false,
                    customer_name:'NaN', 
                    customer_phone:'000',
                    details:b,
                }
                await api
                    .post('/api/laundry/', a)
                    .then(response => {
                        toast({
                            message: 'The laundry has added',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                        this.getData();
                    })
                    .catch(err => {
                        console.log(err)
                    })
                    await this.jeAdd();
                    let x = [];
                    await api
                        .get(`api/last_laundry/`)
                        .then(response => {
                            x = response.data
                        })
                        .catch(error => {
                                console.log(error)
                            })

                    const q = {
                        type_bill:'laundry',
                        cost:this.total_cost,
                        discount:0,
                        bill_item:'laundry'+' '+x.id,
                        item_quantity:this.total_item,
                        trx_no:'',
                        trx_type:'',
                        card_validity:null,
                        card_no:this.res_id,
                        addl_discount:0,
                        vat_amount:this.hotel.laundry_tax,
                        sc_amount:this.hotel.laundry_sc,
                        bill_category:'debit',
                        special_discount:0,
                        special_discount_reason:''
                    }
                     await api
                        .post('/api/bills/',q)
                        .then(() => {
                            
                        })
                        .catch(err => {
                            console.log(err)
                        })
                    this.room_no = '',
                    this.res_id = '',
                    this.ex_date_delivery = '',
                    this.total_item = 0,
                    this.total_cost = 0,
                    this.varr=[],
                    this.addGuest=false;

            },

            async submitCustomerInfo(){
                let b = '';
                for (let i = 0; i < this.varr.length; i++) {
                    b+=this.varr[i].gender+','
                    b+=this.varr[i].item+','
                    b+=this.varr[i].wash+','
                    b+=this.varr[i].quantity+','
                    b+=this.varr[i].cost+'|'
                }
                b = b.slice(0, -1);

                const a = {
                    customer_name: this.customer_name, 
                    customer_phone:this.customer_phone,
                    date_received: this.date_received,
                    ex_date_delivery: this.ex_date_delivery,
                    total_item:this.total_item,
                    total_cost:this.total_cost,
                    status:false,
                    room_no: '',
                    res_id: '',
                    details:b,
                }
                await api
                    .post('/api/laundry/', a)
                    .then(response => {
                        toast({
                            message: 'The laundry has added',
                            type: 'is-success',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        console.log(response);
                        this.getData();
                        this.customer_name = '',
                        this.customer_phone = '',
                        this.ex_date_delivery = '',
                        this.total_item = 0,
                        this.total_cost = 0,
                        this.varr=[],
                        this.addCustomer=false;
                    })
                    .catch(err => {
                        console.log(err)
                    })
            },

            async submit(){
                for (let i = 0; i < this.dataR.length; i++) {
                    if (this.dataR[i].room_no==this.room_no) {
                        this.res_id=this.dataR[i].card_no;
                        this.res_var=this.dataR[i].rev_id;
                        this.roomE = true
                        break;
                    }                
                }
                if(this.roomE==false){
                    toast({
                        message: 'Room is Empty',
                        type: 'is-danger',
                        dismissible: true,
                        pauseOnHover: true,
                        duration: 1000,
                        position: 'bottom-right',
                    })
                    this.res_id=""
                }
                this.roomE = false;
            },
            Add(){
                let b = '';
                let c = 0;
                this.total_item+=this.quantity;
                for (let i = 0; i < this.dataCost.length; i++) {
                    if (this.dataCost[i].item_name==this.item) {
                        if (this.lType=='Wash') {
                            this.total_cost+=(this.dataCost[i].wash_cost*this.quantity)
                            b=this.dataCost[i].wash_cost;
                            c=this.dataCost[i].wash_cost*this.quantity;
                        }
                        if (this.lType=='Dry') {
                            this.total_cost+=(this.dataCost[i].dry_cost*this.quantity)
                            b=this.dataCost[i].dry_cost;
                            c=this.dataCost[i].dry_cost*this.quantity;
                        }
                        if (this.lType=='Iron') {
                            this.total_cost+=(this.dataCost[i].iron_cost*this.quantity)
                            b=this.dataCost[i].iron_cost;
                            c=this.dataCost[i].iron_cost*this.quantity;
                        }
                        break;
                    }  
                }
                if (this.gender=='' || this.item==''||this.wash==''||this.quantity=='') {
                    toast({
                            message: 'Fields cannot blank',
                            type: 'is-warning',
                            dismissible: true,
                            pauseOnHover: true,
                            duration: 2000,
                            position: 'bottom-right',
                        })
                        return;
                }
                const a = {
                    gender:this.gender,
                    item: this.item,
                    wash:this.lType+'('+b+')',
                    quantity:this.quantity,
                    cost:c
                }
                this.varr.push(a)
                this.gender='';
                this.item='';
                this.lType='';
                this.quantity=0;
            },
            Details(t){
                for (let j = 0; j < this.dataA.length; j++) {
                    this.orderCMOdal=t;
                   if (this.dataA[j].id==t) {
                       this.roomM=this.dataA[j].room_no;
                       this.cardM=this.dataA[j].card_no;
                       this.itemTM=this.dataA[j].total_item;
                       this.costM=this.dataA[j].total_cost;
                       let a = this.dataA[j].details;
                       let b = a.split('|')
                       for (let i = 0; i < b.length; i++) {
                           let c = b[i].split(',');
                           const d = {
                               q:c[0],
                               w:c[1],
                               e:c[2],
                               r:c[3],
                               t:c[4],
                           }
                           this.orderD.push(d);
                       }
                       break;
                   }
                }
                this.detailsModalCustomer=true;
               
            },

            DetailsC(t){
                for (let j = 0; j < this.dataA.length; j++) {
                    this.orderCMOdal=t;
                   if (this.dataA[j].id==t) {
                       this.nameM=this.dataA[j].customer_name;
                       this.phoneM=this.dataA[j].customer_phone;
                       this.itemTM=this.dataA[j].total_item;
                       this.costM=this.dataA[j].total_cost;
                       let a = this.dataA[j].details;
                       let b = a.split('|')
                       for (let i = 0; i < b.length; i++) {
                           let c = b[i].split(',');
                           const d = {
                               q:c[0],
                               w:c[1],
                               e:c[2],
                               r:c[3],
                               t:c[4],
                           }
                           this.orderD.push(d);
                       }
                       break;
                   }
                }
                this.detailsModalCustomer2=true;
            },

            async jeAdd(){
                let x = [];
                let dataTT = [];
                let dataCAO = []
                let user = [];
                await api
                    .get(`auth/currentuser/`)
                    .then(response => {
                        user = response.data
                    })
                    .catch(error => {
                            console.log(error)
                        })
                await api
                    .get(`api/last_laundry/`)
                    .then(response => {
                        x = response.data
                    })
                    .catch(error => {
                            console.log(error)
                        })
                await api
                    .get('api/Transaction_table/')
                    .then(response => {
                        dataTT = response.data
                    })
                    .catch(error => {
                        console.log(error)
                    })
                await api
                    .get('api/coa/')
                    .then(response => {
                        dataCAO = response.data
                    })
                    .catch(error => {
                        console.log(error)
                    })
                let c=1;
                if (!dataTT.length==0) {
                    c = parseInt(dataTT[dataTT.length-1].journal_id)+1;
                }
                let q = 0;
                for (let i = 0; i < dataCAO.length; i++) {
                    if (dataCAO[i].cost_center==="laundry" && dataCAO[i].type==="Debit") {
                        console.log(dataCAO[i].id);
                        q=dataCAO[i].id;
                    }
                }
                const a = {
                    description:'Laundry',
                    date:new Date().toISOString().substr(0, 10),
                    account_id: q,
                    credited_by:user.username,
                    reference: this.res_id,
                    credit:0,
                    debit:this.total_cost,
                    memo:x.id,
                    journal_id:c,
                    auto_journal:true
                }
                await api
                .post('/api/Transaction_table/', a)
                .catch(err => {
                    console.log(err)
                })
            },
        }
}
</script>

