<template>
    <div>
        <!-- Title -->
        <div class="columns" style="height:10vh;">
            <div class="column is-12">
                <div class="box" style="display:inline-block; width:100%;">
                    <div style="float:left"><p style="font-size:24px;">Luggage Storage</p></div>
                    <div style="float:right"> <button class="button is-info" @click.prevent="addModal=true">Add</button></div>
                </div>
            </div>
        </div>

        <div class="columns">
            <div class="column is-1"></div>
            <div class="column is-10">
                <table class="table is-fullwidth">
                      <thead>
                          <tr>
                              <th>Tag no.</th>
                              <th>Bag(s)</th>
                              <th>Name</th>
                              <th>Phone</th>
                              <th>Date</th>
                              <th>Time</th>
                              <th>Status</th>
                          </tr>
                      </thead>

                      <tbody>
                          <tr v-for="d in data" v-bind:key="d.id">
                                <td>{{ d.tag_no }}</td>
                                <td>{{ d.bag_no }}</td>
                                <td>{{ d.name }}</td>
                                <td>{{ d.phone }}</td>
                                <td>{{ d.date }}</td>
                                <td>{{ d.time }}</td>
                                <td>
                                    <button class="button is-success is-small" @click.prevent="statusB=true,id=d.id" :disabled="d.status==true">
                                        <p v-if="d.status==true">Delivered</p>
                                        <p v-else>Stored</p>
                                    </button>
                                </td>
                          </tr>
                      </tbody>
                  </table>
            </div>
            <div class="column is-1"></div>
        </div>

        <!-- Modal: room add -->
        <div v-if="addModal == true">
            <div class="modal is-active" style="align-items: start; flex-direction: row; justify-content: end;">
                <div class="modal-background" @click.prevent="addModal = false"></div>
                    <div class="modal-content" style="background:white; max-height: 100%;min-height: 100%; width: 800px;margin-right: 0;">

                        <div class="box" style="height:100vh;">
                            <div class="columns" style="height:10vh;">
                                <div class="column is-12">
                                    <div class="column is-12">
                                        <p style="font-size:20px;">Add Luggage</p>
                                    </div>
                                </div>
                            </div>

                            <div class="columns" style="height:70vh; margin-top:15px;">
                                <div class="column is-12">
                                    <div class="box" style="height:70vh;">

                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Name:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="field">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="text" placeholder="Name" v-model="name">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Phone no.</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="field">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="text" placeholder="phone no." v-model="phone">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Tag No:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="field">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="text" placeholder="tag" v-model="tag">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Bags:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="control">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="number" min=0 v-model="bag">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Date:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="control">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="date" v-model="date">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="field is-horizontal">
                                            <div class="field-label is-normal">
                                                <label class="label" style="font-size:20px;">Time:</label>
                                            </div>
                                            <div class="field-body">
                                                <div class="control">
                                                    <p class="control" style="width:300px;">
                                                        <input class="input" type="time" v-model="time">
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                        
                                    </div>
                                </div>
                            </div>

                            <!-- Buttons -->
                            <div style="margin-top:25px;">
                                <button class="button is-danger is-light" @click.prevent="addModal = false" style="margin-right:5px;">Cancel</button>
                                <button class="button is-success is-light" @click.prevent="Add()" >Submit</button> 
                            </div>
                            
                        </div>
                    </div>
                </div>
        </div>

        <!-- ModaL: Delivery status update -->
        <div v-if="statusB == true">
            <div class="modal is-active">
                <div class="modal-background" @click.prevent="statusB = false">
                    <div class="modal-content" style="height:30vh; background:white; margin-top:25vh;">
                        <div style="margin-top:15%;">
                            <p class="subtitle is-4">Luggage has been Delivered</p>
                            <div style="margin-top:25px">
                                <button class="button is-info is-black" @click.prevent="changeStatus">Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>

    import api from '../boot/axios'
    import { toast } from 'bulma-toast'

export default {
    data(){
        return{
            data:[],
            addModal:false,
            statusB:false,
            name:'',
            phone:'',
            bag:0,
            tag:'',
            id:'',
            date:new Date().toISOString().substr(0, 10),
            time:new Date().toISOString().substr(11, 5),
        }
    },
    created() {
           this.getData();
    },
    methods:{
        async getData() {
                await api
                    .get('api/luggage_store/')
                    .then(response => {
                        this.data = response.data
                        console.table(this.data);
                    })
                    .catch(error => {
                        console.log(error)
                    })
        },

        async Add(){
            if (this.name==''||this.phone=='') {
                toast({
                        message: 'Name/phone is blank',
                        type: 'is-warning',
                        dismissible: true,
                        pauseOnHover: true,
                        duration: 2000,
                        position: 'bottom-right',
                    })
                    return;
            }
            if (this.tag=='') {
                toast({
                        message: 'Tag no is blank',
                        type: 'is-warning',
                        dismissible: true,
                        pauseOnHover: true,
                        duration: 2000,
                        position: 'bottom-right',
                    })
                    return;
            }
            if (this.bag=='') {
                toast({
                        message: 'Bag no is blank',
                        type: 'is-warning',
                        dismissible: true,
                        pauseOnHover: true,
                        duration: 2000,
                        position: 'bottom-right',
                    })
                    return;
            }
                
            const a ={
                tag_no:this.tag,
                bag_no:this.bag,
                status:false,
                name:this.name,
                phone:this.phone,
                date:this.date,
                time:this.time
            }
            await api
                .post('/api/luggage_store/', a)
                .then(() => {
                    toast({
                        message: 'Luggage item added',
                        type: 'is-success',
                        dismissible: true,
                        pauseOnHover: true,
                        duration: 1000,
                        position: 'bottom-right',
                    })

                this.tag='',
                this.bag=0,
                this.name='',
                this.phone='',
                this.date=new Date().toISOString().substr(0, 10),
                this.time=new Date().toISOString().substr(11, 5),
                this.getData();

                this.addModal=false;
                
            })
            .catch(err => {
                console.log(err)
            })

        },
        async changeStatus(){
            await api
                .put(`api/luggage_store/${this.id}/`,{status: true,claimed_date:new Date().toISOString().substr(0, 10),claimed_time:new Date().toISOString().substr(11, 5)})
                .catch(error => {
                        console.log(error)
                    })
                 this.getData();
                 
                this.statusB=false;
        }
    }
}
</script>